export default {
	data() {
		return {
			value: "",
			datas: [],
			url: '',
			page:1,
			pageSize:10
		}
	},
	methods: {
		chooseCustomer(e) {
			const customer = this.datas[e].realname
			uni.setStorage({
				key: 'customer',
				data: customer
			})
			uni.navigateTo({
				url: this.url + '?customer=' + customer
			})
		},
			searchCustomer() {
			if (this.value !== '' && this.value !== null) {
				this.test.post('base/listCustManager', {
					search: this.value,
					page: this.page,
					pageSize: this.pageSize,
				}).then(res => {
					console.log(res)
					if (res.statusCode == 200 && res.data.errorCode == 0) {
						// console.log(res.data.value.length)
						this.datas = res.data.value;
						if (res.data.value.length === 0) {
							uni.showToast({
								title: '没有更多了',
								icon: 'none'
							})
						}
					} else {
		
					}
				}).catch(err => {
					console.log(err)
				})
			} else if (this.inputValue == '' && this.inputValue == null) {
				this.getCompanyInf()
			}
		},
		getCustomerInfo(){
			console.log(this.page,this.pageSize)
			this.test.post('base/listCustManager', {
				search: '',
				page: this.page,
				pageSize: this.pageSize
			}).then(res => {
				console.log(res)
				if (res.statusCode == 200 && res.data.errorCode == 0) {
					this.datas = res.data.value
				}
			}).catch(err => {
				console.log(err)
			})
		},
		more(){
			this.page +=1;
			this.getCustomerInfo()
		}
	},
	onShow(){
		this.getCustomerInfo()
	},
	onLoad(option) {
		this.url = option.address
	}
}
